export default {
  name: 'nearshop',
  components: {
  },
  props: {
    lat: {
      type: Number,
      default: 0
    },
    lng: {
      type: Number,
      default: 0
    },
    shop: {
      type: Object,
      default: function () {
        return {
        'shop_id': '1',
        'shop_name': '',
        'shop_lng': 102.666757,
        'shop_lat': 25.097278,
        'shop_address': '50',
        'shop_img': 'http://store.is.autonavi.com/showpic/5682054ca3104d32a8292aa5',
      }
      }
    }
  },
  data () {
    return {
      msg: '',
      shopInfo: {}
    }
  },
  computed: {
    tel: function(){
      return this.shopInfo.shop_tel ? 'tel:'+this.shopInfo.shop_tel : ''
    },
    juli: function(){
      console.log(this.shopInfo, 'sssssssssssss')
      var juli = this.shopInfo.juli ?  this.shopInfo.juli/ 1000 : 0
      return juli.toFixed(2)
    },
  },
  watch:{
    lat:function (newvalue,oldvalue) {
      var self = this
      self.shopInfo = this.shop
      if(this.lat && this.lng){
        self.xarpost('Shopinfo/getNearShop', {type: 1, lat: self.lat, lng: self.lng}).then( res => {
          let {data, error, message} = res
          self.shopInfo = data
        })
      }
    },
    shop: function (newvalue, oldvalue){
      this.shopInfo = newvalue
    }
  },
  created: function() {
    this.shopInfo = this.shop
  },
  methods: {
    daohang: function(){
      var self = this
      this.$wechat.openLocation({
        latitude: parseFloat(self.shopInfo.shop_lat), // 90 ~ -90
        longitude: parseFloat(self.shopInfo.shop_lng), // 180 ~ -180
        name: self.shopInfo.shop_name, // 
        address: self.shopInfo.shop_address, // 
        scale: 14, // ,,1~28
        infoUrl: '' // ,
      });
    }
  }
}



